export class Cycanswer {
    private nl:string;
    private cycl:string;
    private mricode:string;
    private recommended:boolean;
    
    constructor(naturalLang:string,cycLang:string,mriCode:string,recommended:boolean) {
        this.nl = naturalLang;
        this.cycl = cycLang;
        this.mricode = mriCode;
        this.recommended = recommended;
    }
}
